
import { defineComponent } from "vue";
import { Task } from "@/interfaces/Task"; // Interface Task is defined in mevn-typescript\src\interfaces\Task.ts
import TaskService from "../../services/TaskService";

export default defineComponent({
  setup() {
    return {};
  },
  data() {
    return {
      tasks: [] as Task[], // Array of Task objects
      task: {} as Task, // task is an object of type Task
      loading : true,
      create: false,
      error : false
    };
  },
  mounted() {
    console.log("TaskForm mounted");
    TaskService.getTasks().then((res) => {
      this.tasks = res;
    }).catch((err) => {
      console.log(err);
    })
  },
  methods: {
    saveTask() {
      TaskService.createTask(this.task).then((res) => {
        this.tasks.push(res);
        this.task = {} as Task;
        this.loading = true
        this.create = true
      }).catch((err) => {
        this.loading = true
        this.create = false
        this.error = true
        console.log(err);
      });
      this.loading = false
      console.log("submit");
    },
  },
});
